:root {
  --fontChillax: "Chillax", sans-serif;
  --fontSatoshi: "Satoshi", sans-serif;
  --fontGambetta: "Gambetta", serif;
  --textMuted: #dac5a799;
  --textDefault: #dac5a7;
  --bgColor: #0e0e0e;
  --fontSize: 16px;
  --minWidth: 320px;
  --maxWidht: 1920px;
  --maxWidthContainer: 1400px;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  max-width: var(--maxWidht);
  font-family: var(--fontChillax);
  font-weight: 400;
  font-size: var(--fontSize);
  color: var(--textDefault);
  background-color: var(--bgColor);
}

.container {
  max-width: var(--maxWidthContainer);
  margin: 0 auto;
}

.btn__link {
  cursor: pointer;
  color: #dac5a7;
  font-family: var(--fontSatoshi);
  letter-spacing: 1.5px;
  text-transform: uppercase;
  align-items: center;
  gap: 12px;
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: 160%;
  display: inline-flex;
}

.btn__link div {
  background: #dac5a70d;
  border: 1px solid #dac5a726;
  border-radius: 500px;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  transition: all .3s cubic-bezier(.075, .82, .165, 1);
  display: flex;
}

.btn__link:hover div, .btn__link:focus div {
  border: 1px solid #dac5a740;
  transform: rotate(45deg);
}

.flex-between {
  justify-content: space-between;
  display: flex;
}

.btn {
  color: #0e0e0e;
  font-family: var(--fontSatoshi);
  letter-spacing: 1.5px;
  text-transform: uppercase;
  background: #dac5a790;
  border-radius: 2px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding: 10px 16px;
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: 160%;
  transition: background .3s cubic-bezier(.075, .82, .165, 1);
  display: flex;
}

.btn:hover, .btn:focus {
  background: #dac5a7;
}

.btn svg, .btn svg {
  transition: transform .3s cubic-bezier(.075, .82, .165, 1);
}

.btn:hover svg, .btn:focus svg {
  transform: rotate(45deg);
}

.header {
  z-index: 100;
  justify-content: center;
  display: inline-flex;
  position: fixed;
  top: 48px;
  left: 0;
  right: 0;
}

.header__container {
  backdrop-filter: blur(15px);
  background-color: #dac5a733;
  border: 1px solid #dac5a726;
  border-radius: 2px;
  justify-content: center;
  gap: 32px;
  padding: 12px 12px 12px 24px;
  display: inline-flex;
}

.logo-link {
  cursor: pointer;
  padding-top: 10px;
  padding-bottom: 10px;
  transition: transform .3s cubic-bezier(.075, .82, .165, 1);
}

.logo-link:hover, .logo-link:focus {
  transform: scale(1.1);
}

.logo-link svg {
  width: 53px;
  height: 21px;
}

.nav {
  align-items: center;
  gap: 16px;
  display: flex;
}

.mobile-nav {
  display: none;
}

.nav__list {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

.nav__link {
  font-family: var(--fontSatoshi);
  letter-spacing: 1.5px;
  text-transform: uppercase;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 13px;
  font-weight: 300;
  line-height: 160%;
  position: relative;
}

.nav__link:before {
  content: "";
  background-color: #dac5a8;
  width: 0;
  height: 1px;
  transition: width .5s cubic-bezier(.075, .82, .165, 1);
  position: absolute;
  bottom: 7px;
}

.nav__link:hover:before, .nav__link:focus:before {
  width: 100%;
}

.nav__button {
  color: #0e0e0e;
  min-width: 111px;
  font-family: var(--fontSatoshi);
  letter-spacing: 1.5px;
  text-transform: uppercase;
  background: #dac5a7;
  border-radius: 2px;
  padding: 10px 16px;
  font-size: 13px;
  line-height: 160%;
  transition: transform .3s cubic-bezier(.075, .82, .165, 1);
}

.nav__button:hover, .nav__button:focus {
  transform: scale(1.05);
}

@media screen and (width <= 768px) {
  .header {
    top: 24px;
  }

  .mobile-nav {
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding-right: 53px;
    display: flex;
    position: relative;
  }

  .mobile-nav__list {
    display: none;
  }

  .mobile-nav__list.mobile--active {
    flex-direction: column;
    justify-content: space-around;
    gap: 20px;
    display: flex;
  }

  .desctop-nav {
    display: none;
  }

  .header__container {
    justify-content: space-between;
    width: 100%;
    padding: 12px;
  }

  .btn-nav {
    flex-direction: column;
    justify-content: space-between;
    gap: 5px;
    width: 40px;
    height: 40px;
    padding: 10px 5px;
    display: flex;
  }

  .menu__btn {
    z-index: 10;
    background-color: #0000;
    border: none;
    flex-direction: column;
    gap: 7px;
    width: 40px;
    height: 40px;
    padding: 10px 5px;
    display: flex;
    position: absolute;
    top: 4px;
    right: 0;
  }

  .menu__btn span {
    background-color: var(--textDefault);
    width: 100%;
    height: 3px;
  }

  .menu__btn.btn--active span {
    transition: all .2s;
  }

  .menu__btn.btn--active span:nth-child(2) {
    background-color: #0000;
  }

  .menu__btn.btn--active span:first-child {
    transform: rotate(45deg)translate(6px, 7px);
  }

  .menu__btn.btn--active span:nth-child(3) {
    transform: rotate(-45deg)translate(6px, -7px);
  }
}

